import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { DashboardDTO } from '../Model/DashboardDTO';
import { API_ENDPOINTS, API_URL } from '../Model/Global';
import { BaseApiHandlerService } from './base-api-handler.service';

@Injectable({
  providedIn: 'root'
})
export class DashboardService extends BaseApiHandlerService{
  dashboardObserver$:Observable<DashboardDTO>;
  private dashboardSubject$: BehaviorSubject<DashboardDTO> = new BehaviorSubject<DashboardDTO>(undefined);
  overallInfoObserver$:Observable<any>;
  private overalllSubject$:BehaviorSubject<any> =new BehaviorSubject<any>(undefined);

  constructor(private http:HttpClient) {

    super(http);
    this.dashboardObserver$= this.dashboardSubject$.asObservable();
    this.overallInfoObserver$=this.overalllSubject$.asObservable();
  }

  GetAnalytics()
  {
    this.GetAll(API_URL+API_ENDPOINTS.Transaction+"/GetAnalytics").subscribe(res=>{
      this.dashboardSubject$.next(res.dynamicResult);
    });
  }
  GetOverAllInfo(){
    this.GetAll(API_URL+API_ENDPOINTS.Transaction+"/OverallInfo").subscribe(res=>{
      this.overalllSubject$.next(res.dynamicResult);
    })
  }
  getRevenuePerMonthData(date)
  {
    var url = API_URL + API_ENDPOINTS.Transaction + `/GetRevenuePerMonthData?date=${date}`;
    return this.GetAll(url);
  }
}
